* {
	margin: 0;
	max-width: 100%;
}

body {
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
		'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.footer-left{
    position: relative;
    display: flex;
    
    flex-grow: 1;
    color: rgb(250, 250, 250);
    pointer-events: none;
    margin-left:15px;
    margin-bottom: 30px;
    z-index: 100;
}
.ticker{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 60%;
    display: flex;
    align-items: center;
}
.ticker marquee{
    display: flex;
    align-items: center;
}
.text{
    width: 100%;
}
.text h3 {
    padding-bottom: 20px;
}
.text p{
        padding-bottom: 20px;

}
.footer-right {
	z-index: 100;
	color: white;
	margin-right: 15px;
}

.sidebar-icon {
	width: 50px;
	display: flex;
	margin-bottom: 30px;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
}

.record {
	-webkit-animation: spinTheRecord infinite 5s linear;
	        animation: spinTheRecord infinite 5s linear;
	-webkit-filter: invert(1);
	        filter: invert(1);
}

.record-below {
	margin-bottom: 70px;
}

@-webkit-keyframes spinTheRecord {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes spinTheRecord {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

.video{
    background-color: rgb(3, 0, 0);
    width: 100%;
    height: 100%;
    text-shadow:  0 0 4px rgba(0, 0, 0, 0.5);
    scroll-snap-align: start;
    position: relative;

}
.player{
    position: absolute;
    object-fit: fill;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.bottom-controls{
    display: flex;
    align-items: flex-end;
    height: 100%;
}



.app{
  display: grid;
  background-color: black;
  place-items: center;
}
.container{
  position: relative;
  height: 90vh;
  width: 50.625vh;
  overflow: scroll;
  border-radius: 20px;
}
.container::-webkit-scrollbar {
display: none;
}
.container {
-ms-overflow-style: none; /* IE and Edge */
scrollbar-width: none; /* Firefox */
}
html{
-ms-scroll-snap-type: y mandatory;
    scroll-snap-type: y mandatory;}
